
import exportDom from './createDom'


/**
 * node
 * @param {*} node 
 * @returns 
 */
export function createImageNode(node) {
  const { urlStr, tooltip, ports, group, width, height, typeImgNo, y = 50, x = 120, defaultSpanStyle } = node
  let protsArr = []
  if (ports.length) {
    for (let i = 0; i < ports.length; i++) {
      let obj = {
        group: group,
        id: (group + i),
        args: ports[i].args
      }
      protsArr.push(obj)
    }
  }
  return {
    shape: "html",
    x,
    y,
    width: width,
    height: height,
    data:{
      typeImgNo,
      textContent: tooltip,
      defaultSpanStyle
    },
    ports: {
      items: protsArr,
      groups: {
        [group]: {
          attrs: {
            circle: {
              r: 6,
              magnet: true,
              stroke: '#31d0c6',
              fill: '#fff',
              strokeWidth: 2
            }
          },
          position: {
            name: 'absolute'
          }
        }
      }
    },
    // html: nodeStr
    html: () => exportDom({urlStr, tooltip, ports, group, width, height, typeImgNo, defaultSpanStyle})
  //   html: () => {
  //     const wrap = document.createElement("div");
  //     const span = document.createElement("span");
  //     span.textContent = tooltip;
  //     span.style.cssText = spanCss;
  //     const imgEl = document.createElement('img')
  //     imgEl.src = require(`${urlStr}`);
  //     imgEl.style.cssText = "width: 100%;height: 100%;"
  //     wrap.setAttribute("class", 'nodeBox')
  //     wrap.appendChild(imgEl)
  //     wrap.appendChild(span);
  //     wrap.style.cssText = 'width: 100%;height: 100%;display: flex;align-items: center;justify-content: center;border-radius: 4px;';
  //     return wrap;
  //   },
  }
}